export default {
	data() {
		return {};
	},
	methods: {
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// 上拉加载
	onReachBottom: function() {
		console.log('xia');
	}
};
